<template>
  <div class="pdct-l-wp layout" v-if="tabs.length">
    <TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
    <TheContentList :message='message' @toDetail='handleToDetail' v-if='showList'></TheContentList>
		<div class="noFoundProduct" v-else>
			<div class="ctx">
				<img src="@/assets/images/product-icon.png">
				该分类下暂无产品
			</div>
		</div>
		<ThePaginationBar></ThePaginationBar>
  </div>
</template>

<script>
import TheCompiler from '../components/product/list/TheCompiler.vue'
import TheContentList from '../components/product/list/TheContentList'
import ThePaginationBar from '@/components/ThePaginationBar.vue';
export default {
  data () {
    return {
      tabs: [],
      message: [],
			showList: true
    }
  },
  methods: {
    handleChangeTab (item) {
      this.$router.push('/37duc/product/list?type=' + item.id)
    },
    handleToDetail (id) {
      this.$router.push({
        path: '/37duc/product/detail/' + id,
        query: {
          productId: id
        }
      })
    },
    getMessage (productCategoryId, page, limit) {
      return new Promise((resolve, reject) => {
        this.$api.product.getProductListWidthType({
          productCategoryId: productCategoryId,
          page: page,
          limit: limit
        }).then(data =>{
					console.log(data)
					resolve(data)
				})
      })
    },
    renderList (data) {
      let message = []
      data.forEach(val => {
        let item = {}
				try{
					item.name = val.name
					item.marks = [val.feature1, val.feature2, val.feature3]
					item.price = val.price
					item.character = []
					val.parameter_values[0].entries.forEach(entry => item.character.push({ ...entry }))
					item.insure_condition = val.insure_condition
					item.id = val.id
				}catch(e){
					//TODO handle the exception
					// console.error(e)
				}
        message.push(item)
      })
      this.message = message
    },
    init () {
      let type = this.$route.query.type
			if (type === undefined) { this.$router.replace('/37duc/product/list?type=' + this.tabs[0].id);return}
      if (!this.tabs.find(tab => tab.id == type)) {
        this.$router.replace('/37duc/404')
        return
      }
      this.getMessage(type, 1, 6)
        .then(data => {
					console.log(data)
					if(data.data.length) {
						this.showList = true;
						this.renderList(data.data)
					}else{
						this.showList = false;
					}
        }).catch(err=>{})
      this.tabs.forEach(tab=>tab.active=tab.id==type)
    }
  },
  created () {
    this.$api.product.getProductType()
      .then((data) => {
        data.forEach((v, k) => this.tabs.push({
          tabTxt: v.name,
          active: k == 0,
          id: v.id
        }))
        if (!this.tabs.length) { this.$router.replace('/37duc/404'); return }
        this.init()
      })
  },
  watch: {
    '$route': {
      handler (val) {
        this.init()
      }
    }
  },
  components: {
    TheCompiler,
    TheContentList,
		ThePaginationBar
  }
}
</script>

<style lang="scss" scoped="scoped">
  .pdct-l-wp {
    wdith: 1100px;
    margin: 12px auto;
		.noFoundProduct{
			min-height: 626px;
			background: #FFFFFF;
			box-shadow: 0 2px 4px 0 rgba(219,219,219,0.35);
			position: relative;
			.ctx{
				position: absolute;
				left: 0;
				top: 50%;
				margin-top: -60px;
				width: 100%;
				text-align: center;
				font-size: 20px;
				color: #D5D8D8;
				line-height: 20px;
				img{
					display: block;
					margin: 0 auto;
					width: 64px;
					height: 72px;
					margin-bottom: 28px;
				}
			}
		}
  }
</style>
